import React from 'react';
import './App.css'

var PostRegistration = () => {
    return (
        <>
            <h1>Create New Post</h1>
            <form onSubmit="">
                <input name="postTitle" placeholder="Post title goes here.." className="newPostTitle"/>
                <br/><br/>
                <input name="postContent" placeholder="Post content goes here.." className="newPostBody"/>
                <br/>
                <button>Save Post</button>
            </form>
        </>
    )
}


export default PostRegistration
