import React from 'react';
import './App.css';
import posts from './PostsData';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";



var SinglePost = (props) => {
    return (
        <div className="blogPosts">
            <div>
                <img src={props.image} alt="X"/>
                <h3>{props.title}</h3>
                <p>{props.startContent}<strong>{props.strongText}</strong>{props.middleText}<span className="red">{props.redText}</span>
                    {props.mostContent}<br/>{props.lastContent}<Link to={props.link}>{props.linkDescription}.</Link></p>
                <p className="published">{props.published}</p>
            </div>
        </div>
    )
}

var Posts = () => {
    return (
        <Router>
            {posts.map((post) =>{
            return <SinglePost
                title={post.title}
                startContent={post.startContent}
                strongText={post.strongText}
                middleText={post.middleText}
                redText={post.redText}
                mostContent={post.mostContent}
                lastContent={post.lastContent}
                linkDescription={post.linkDescription}
                image={post.image}
                published={post.published}
                link={post.link}
                />
        })}
        </Router>
    )
}


export default Posts;